a {
    text-decoration: none;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news {
    width: 48.5%;
    padding: 12px 20px 20px 20px;
    box-sizing: border-box;
    background: #f4f7fb;
    border-radius: 8px;
}
.gk_home20-wrapper .gknews_box[type="newest"] h3 {
    padding-bottom: 8px;
    margin-bottom: 15px;
    border-bottom: 1px solid #d6dce2;
    box-sizing: border-box;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news-title {
    font-size: 14px;
    line-height: 140%;
    text-transform: uppercase;
    color: #555;
    font-family: SFD-Bold;
    position: relative;
    display: block;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn {
    display: flex;
    flex-direction: column;
    list-style: none;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn .nn-item {
    padding-bottom: 12px;
    border-bottom: 1px dashed #d6dce2;
    margin-bottom: 12px;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn .nn-item .item-gr {
    display: flex;
}
.gk_home20-wrapper
    .gknews_box[type="newest"]
    .noticeable-news
    .list-nn
    .nn-item.first-noticeable-news
    .item-gr
    .item-news {
    flex-direction: column;
}

.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn .nn-item .item-gr .item-news {
    width: 100%;
}
.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn .nn-item .item-gr .item-news {
    display: flex;
    justify-content: space-between;
}
.gk_home20-wrapper
    .gknews_box[type="newest"]
    .noticeable-news
    .list-nn
    .nn-item.first-noticeable-news
    .item-gr
    .item-news
    .item-news-thumb {
    width: 100%;
}

.gk_home20-wrapper .gknews_box[type="newest"] .noticeable-news .list-nn .nn-item .item-gr .item-news .item-news-thumb {
    display: block;
}
.gk_home20-wrapper
    .gknews_box[type="newest"]
    .noticeable-news
    .list-nn
    .nn-item
    .item-gr
    .item-news
    .item-news-thumb
    i {
    display: block;
    width: 100%;
    padding-top: 61.5%;
    position: relative;
    margin-bottom: 10px;
    border-radius: 4px;
    overflow: hidden;
}
.gk_home20-wrapper
    .gknews_box[type="newest"]
    .noticeable-news
    .list-nn
    .nn-item
    .item-gr
    .item-news
    .item-news-thumb
    i
    img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
